import React, { useRef, useCallback, useEffect, useMemo, useState } from 'react'
import { range } from 'lodash'
import Measure from 'react-measure'
import videoCanvas from 'video-canvas';

import Box from '../../components/Box'
import Text from '../../components/Text'

import mp4 from './KVmotion.mp4'
import webm from './KVmotion.webm'

import banner from '../HomePage/banner.png'
import underBanner from '../HomePage/under-banner.png'

const PIXEL_RATIO = (function () {
  if (typeof window === 'undefined') return 1
  const ctx = document.createElement('canvas').getContext('2d')
  const dpr = window.devicePixelRatio || 1
  const bsr = ctx.webkitBackingStorePixelRatio ||
        ctx.mozBackingStorePixelRatio ||
        ctx.msBackingStorePixelRatio ||
        ctx.oBackingStorePixelRatio ||
        ctx.backingStorePixelRatio || 1;

  return dpr / bsr;
})();

const videoSize = {
  width: 1920,
  height: 1080,
}

const videoRadio = videoSize.height / videoSize.width

const Loading = ({ onDone, onLoaded }) => {
  const [progress, setProgress] = useState([])
  const [playing, setPlaying] = useState()
  const [size, setSize] = useState({})
  const canvasRef = useRef()
  const videoRef = useRef()
  const loader = useCallback((asset, i) => {
    const req = new XMLHttpRequest();
    req.onprogress = evt => {
      if (evt.lengthComputable) {
        setProgress(p => {
          const newP = [...p]
          newP[i] = [evt.loaded, evt.total]
          return newP
        })
      }
    };

    req.open('GET', asset, true);
    req.send();
  }, [])

  const loaded = useMemo(() => {
    if (!progress.length) return 0
    const total = progress.reduce((t, p) => {
      t[0] += p[0]
      t[1] += p[1]
      return t
    }, [0, 0])
    return Math.floor(total[0] / total[1] * 100)
  }, [progress])

  useEffect(() => {
    [
      window.Modernizr.video.webm ? webm : mp4,
      banner,
      underBanner,
    ].forEach((url, i) => loader(url, i))
  }, [])

  useEffect(() => {
    if (loaded === 0) {
      if (onLoaded) onLoaded()
      setTimeout(() => setPlaying(true), 500)
    }
  }, [loaded])

  useEffect(() => {
    if (videoRef.current) {
      canvasRef.current.getContext('2d').setTransform(PIXEL_RATIO, 0, 0, PIXEL_RATIO, 0, 0);
      videoRef.current.onended = () => {
        if (onDone) onDone()
      }
      videoCanvas(videoRef.current, {
        canvas: canvasRef.current,
        updateSize: false,
      });
      // videoRef.current.play();
      // videoRef.current.pause();
      canvasRef.current.onclick = () => {
        if (videoRef.current) {
          videoRef.current.play();
        }
      }
    }
  }, [playing])
  const videoPlayerSize = useMemo(() => {
    if (size.width && size.height) {
      if (videoRadio < size.height / size.width) {
        return { width: size.width, height: size.width * videoRadio }
      } else {
        return { width: size.height / videoRadio, height: size.height }
      }
    }
    return {}
  }, [size])
  return (
    <Box.Fixed
      left="0"
      top="0"
      right="0"
      bottom="0"
      bg="primary"
      zIndex={10000}
      id="loader"
    >
      {playing ? (
        <Measure bounds onResize={({ bounds }) => setSize(bounds)}>
          {({ measureRef }) => (
            <Box.Relative height="100%" ref={measureRef}>
              <video
                {...videoSize}
                muted
                autoPlay
                playsInline
                ref={videoRef}
                style={{ opacity: 0, width: 1, height: 1 }}
              >
                <source src={webm} type="video/webm" />
                <source src={mp4} type="video/mp4" />
              </video>
              <Box.AbsCenter
                top="50%"
                left="50%"
                transform="translate(-50%, -50%)"
                bg={loaded ? 'white' : 'black'}
                style={videoPlayerSize}
                transition={`all 100ms ease-in-out`}
              >
                <canvas
                  ref={canvasRef}
                  style={{ width: '100%', height: '100%' }}
                  width={videoPlayerSize.width * PIXEL_RATIO}
                  height={videoPlayerSize.height * PIXEL_RATIO}
                />
              </Box.AbsCenter>
            </Box.Relative>
          )}
        </Measure>
      ) : (
        <Box.Relative top="50%" color="white" textAlign="center" transition="transform 200ms" transform={`translateY(-${loaded}%)`}>
          {range(101).map(d => (
            <Box key={d}>
              <Box.Inline borderBottom={loaded === d && '4px solid white'}>
                <Text fontSize="5em">{100 - d}</Text>
              </Box.Inline>
            </Box>
          ))}
        </Box.Relative>
      )}
    </Box.Fixed>
  )
}

export default Loading
