import { theme, extendTheme } from "@chakra-ui/react";
import { createBreakpoints } from "@chakra-ui/theme-tools"

import get from 'lodash/get';

import memorize from '../../utils/memorize'

export const breakpoints = createBreakpoints({
  // xs: "352px",
  sm: "768px",
  md: "992px",
  lg: "1280px",
  xl: "1920px",
})

export const responsiveIndex = [
  [0, 'mobile'],
  [2, 'laptop'],
  [3, 'desktop'],
]

const responsiveMap = breakpoints.map((_, i) => {
  const id = responsiveIndex.findIndex(([ri]) => ri + 1 > i)
  return id >= 0 ? id : responsiveIndex.length
})

export const responsive = memorize((...args) => {
  const argsLen = args.length
  if (argsLen <= 1) return args[0]
  return breakpoints.map((_, i) => get(args, [responsiveMap[i]], null))
});

export const mobileOrDesktop = responsive

const font = '"GenSekiGothic TW", "Noto Sans TC", "PingFang TC", "HeiTi TC", "Microsoft JhengHei", sans-serif';
const fonts = {
  heading: font,
  body: font,
  mono: "Menlo, monospace",
}

const lightGray = '#E6E6E6';
const gray = '#979797';
const darkGray = '#211815';
const prussianBlue = '#002A5E';
const brightBlue='#0074B7'
const ceruleanBlue = '#13E6F0';
const darkViolet = '#7D3787';
const vividViolet = '#A446BA'
const gigas = '#4F3398'
const mustard = '#FFDC53'
const pinkSalmon = '#FF90A1'
const mandy = '#df536A'
const disco = '#B71962'
const apple = '#63c532'
const sun = '#F8A908'
const yellow = '#F5A737'
const monza = '#FF5555'
const conifer = '#bae659'
const jungleGreen = '#00796d'
const bannerBg = '#00609F'
const lightGreen = '#B3D476'
const lightPurple = '#CABFEC'
const red = '#FF5555'
const bgBlue= '#7688A3'
const lakeBlue = '#82CAD0'

const overrides = {
  fonts,
  colors: {
    ...theme.colors,
    bannerBg,
    primary: prussianBlue,
    secondary: ceruleanBlue,
    danger: disco,
    text: get(theme.colors, 'black'),
    lightGray,
    lightGreen,
    lightPurple,
    sun,
    bgBlue,
    lakeBlue,
    apple,
    prussianBlue,
    ceruleanBlue,
    gray,
    red,
    yellow,
    darkGray,
    brightBlue,
    darkViolet,
    vividViolet,
    gigas,
    mustard,
    conifer,
    pinkSalmon,
    mandy,
    disco,
    monza,
    jungleGreen,
  },
  breakpoints,
}

const customTheme = extendTheme(overrides)

export default customTheme
